import styles from "./PriceList.module.scss";

import { PriceCard } from './PriceCard'

export function PriceList() {
    return (
        <section className={styles.price_list} id="priceList">
            <div className={styles.container}>
                <div className={styles.about}>
                    <h2 className={styles.title}>-</h2>
                    <div className={styles.background__blur}></div>
                </div>
                <div className={styles.offers}>
                    <PriceCard title="IONKID One" description="  " />
                    <PriceCard title="IONKID ABA" description="    -" />
                    <PriceCard title="IONKID Standart" description="    " />
                    <PriceCard title="IONKID Advanced" description="      " popular={true} />
                    <PriceCard title="IONKID Special" description="   " />
                </div>
            </div>
        </section>
    )
}
